import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import connect from '@vkontakte/vkui-connect';
import { View, Panel,  PanelHeader, platform, FixedLayout, Tabs, TabsItem, IOS, ANDROID } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Navigation from 'D:/Ekaterina/SourceCode/nodejs-app/mfc-service-home/src/panels/Navigation';
import Services from 'D:/Ekaterina/SourceCode/nodejs-app/mfc-service-home/src/panels/Services';
import Entry from 'D:/Ekaterina/SourceCode/nodejs-app/mfc-service-home/src/components/Entry';
import User from 'D:/Ekaterina/SourceCode/nodejs-app/mfc-service-home/src/components/User';

const osname = platform();

class App extends React.Component {
  constructor(props) {
		super(props);

		this.state = {
      activePanel: 'navigation',
			fetchedUser: null,
		};
	}

  componentWillMount() {
    connect.subscribe((e) => {
      switch (e.detail.type) {
        case 'VKWebAppGetUserInfoResult':
          this.setState({ fetchedUser: e.detail.data });
          break;
        default:
          console.log(e.detail.type);
      }
    });
    connect.send('VKWebAppGetUserInfo', {});
  }

  go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};


  render() {
    return (
      <View activePanel={this.state.activePanel}>
				<Navigation id="navigation" fetchedUser={this.state.fetchedUser} go={this.go}/>
        <Services id="services" go={this.go}/>
			</View>
    );
  }
}

export default App;

